* {
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif
}

#root {
    display: flex;
    flex-direction: column;
}

h2 {
    margin-bottom: 0;
}

span {
    padding-bottom: 10px;
}

body {
    padding: 20px;
    font-size: large;
}

input {
    width: 100%;
    font-size: 1.2rem;
    color: grey;
}

.card {
    display: flex;
    flex-direction: column;
    align-content: space-between;
    border: 1px solid grey;
    border-radius: 20px;
    overflow: hidden;
    margin-bottom: 10px;
    margin-right: 10px;
}

.card .title {
    display: flex;
    flex-grow: 1;
    
    align-items: center;
}

.card .title p {
    width: 0;
    flex-grow: 1;
    padding: 5px;
    text-align: center;
}

.card img {
    max-height: 200px;
}

.card-grid {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
}

.card:hover {
    background-color: greenyellow;
}

footer {
    margin-top: 50px;
    align-self: center;
}